import { Contract } from '@ethersproject/contracts'
import { TransactionResponse } from '@ethersproject/providers'
import { BigNumber } from 'ethers'
import { useMemo } from 'react'

export default function useContractCall(contract: Contract): (
  method: string,
  params: Array<any>,
  options?: { value?: BigNumber; gasLimit?: BigNumber }) => Promise<TransactionResponse> {

  return useMemo(() => {
    return async (
      method: string,
      params: Array<any>,
      options?: { value?: BigNumber; gasLimit?: BigNumber }
    ) => {
      return contract[method](...params, options)
    }
  }, [contract])
}
