/* eslint-disable react/jsx-no-target-blank */
import { useState, useEffect, useMemo, useRef, useCallback } from 'react'
import { useParams } from 'react-router-dom'
import { inject, observer } from 'mobx-react'
import { useAlert } from 'react-alert'
import Tab from '../../components/Tab/Tab'
import { bg, formatAddress, getAmountETHAndDeri, getGasLimit } from "../../utils/utils"
import config from '../../config.json'
import downIcon from '../../assets/img/arrow-down.svg'
import useConfig from '../../hooks/useConfig'
import { DeriEnv } from '../../lib/web3js/index'
import './detail.less'
import './claim.less'
import backIcon from '../../assets/img/back.svg'
import ARBITRUM from '../../assets/img/arbitrum-logo.svg'
import deri from './img/deri.svg'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import noNFT from './img/no-nft.svg'
import Spinner from '../../components/Spinner/Spinner'
import Icon from '../../components/Icon/Icon'
import Search from '../../components/Search/Search'
import Button from '../../components/Button/Button'
import { EVENT_TRANS_BEGIN, EVENT_TRANS_END, PRIMARY } from '../../utils/Constants'
import useContract from '../../hooks/useContractLp'
import { BigNumber } from 'ethers'
import './Detail/operateLiquidity.less'
import useContractCall from '../../hooks/useContractCall'
import Emitter from '../../utils/Emitter'

const env = DeriEnv.get();
const { chainInfo } = config[env]
const chainConfig = config[DeriEnv.get()]['chainInfo']
const DeriAddress = "0x21E60EE73F17AC0A411ae5D690f908c3ED66Fe12"
const ETHAddress = "0x82af49447d8a07e3bd95bd0d56f35241523fbab1"
const PairAddress = "0x96C3966534b4377691e11e163A0409Ad01667343"
function LpDetail({ lang, wallet = {}, poolManager, getLang }) {
  const { chainId, address } = useParams();
  const [isShowInfo, setIsShowInfo] = useState('show')
  const alert = useAlert();
  const nowToken = "UNISWAP-LP"
  const [poolConfig, setPoolConfig] = useState('')
  const [bTokenList, setBTokenList] = useState([])
  const [liquidity, setLiquidity] = useState("")
  const [poolDeri, setPoolDeri] = useState(0)
  const config = useConfig();
  const [claimPending, setClaimPending] = useState(false)
  const [unStakedTokenIdList, setUnStakedTokenIdList] = useState([])
  const [stakedTokenIdList, setStakedTokenIdList] = useState([])
  const [aTimeRewardInfo, setATimeRewardInfo] = useState([])
  const [bTimeRewardInfo, setBTimeRewardInfo] = useState([])
  const [totalYearDeri, setTotalYearDeri] = useState(0)
  const [accountEthTokenValue, setAccountEthTokenValue] = useState(0)
  const [accountDeriToken, setAccountDeriToken] = useState(0)
  const [activeIndex, setActiveIndex] = useState(0)
  const [apy, setApy] = useState()
  const networkText = chainInfo[chainId] && chainInfo[chainId].name;

  const DeriforPairContract = useContract("ERC20", chainId, DeriAddress)
  const EthforPairContract = useContract("ERC20", chainId, ETHAddress)
  const NFTContract = useContract("IERC721", chainId)
  const PoolContract = useContract("arbitrumLp", chainId)
  const BTokenContract = useContract("LpTokenId", chainId)
  const PairContract = useContract("PairAbi", chainId)

  const unTokenIdListRef = useRef()
  const tokenIdListRef = useRef()

  const getPendingReward = async () => {
    let res = await PoolContract["getRewardInfo(address)"](wallet.detail.account)
    if (res) {
      let deriNumber = BigNumber.from(res._hex).toString() / (10 ** 18)
      setPoolDeri(deriNumber)
    }
  }

  const bTime = useCallback(async (id) => {
    let res;
    await setTimeout(async () => {
      let data = stakedTokenIdList.map(async (item) => {
        let obj = {}
        let res = await PoolContract.getSecondsPerLiquidityInsideX128(item)
        if (res) {
          obj["id"] = item
          obj["timestamp"] = BigNumber.from(res.timestamp._hex).toString()
          obj["secondsPerLiquidityInsideX128"] = BigNumber.from(res.secondsPerLiquidityInsideX128._hex).toString()
        }
        return obj
      })
      data = await Promise.all(data)
      setBTimeRewardInfo(data)
    }, 5000);
    return res
  }, [stakedTokenIdList, PoolContract])

  const aTime = useCallback(async () => {
    let data = stakedTokenIdList.map(async (item) => {
      let obj = {}
      let res = await PoolContract.getSecondsPerLiquidityInsideX128(item)
      if (res) {
        obj["id"] = item
        obj["timestamp"] = BigNumber.from(res.timestamp._hex).toString()
        obj["secondsPerLiquidityInsideX128"] = BigNumber.from(res.secondsPerLiquidityInsideX128._hex).toString()
      }
      return obj
    })
    data = await Promise.all(data)
    bTime()
    setATimeRewardInfo(data)
  }, [stakedTokenIdList, PoolContract])

  const getBTokenValue = useCallback(async () => {
    let data = stakedTokenIdList.map(async (item) => {
      let obj = {}
      let res = await BTokenContract.positions(item)
      obj = {
        tickLower: res.tickLower,
        tickUpper: res.tickUpper,
        liquidity: BigNumber.from(res.liquidity._hex).toString()
      }
      return obj
    })
    data = await Promise.all(data)
    let tickCurrent = await PairContract.slot0()
    tickCurrent = tickCurrent.tick
    let amount = data.map((item) => {
      let [total_amount0, total_amount1] = getAmountETHAndDeri(tickCurrent, item.tickLower, item.tickUpper, item.liquidity)
      return {
        deri: total_amount0 / 10 ** 18,
        eth: total_amount1 / 10 ** 18
      }
    })
    let deriSum = amount.reduce((accumulator, currentValue) => {
      return accumulator + currentValue.deri;
    }, 0)
    let ethSum = amount.reduce((accumulator, currentValue) => {
      return accumulator + currentValue.eth;
    }, 0)
    let ethPath = "https://api.coingecko.com/api/v3/simple/price?ids=ethereum&vs_currencies=USD"
    let eresp = await fetch(ethPath);
    let eres = await eresp.json();
    let ethPrice = eres.ethereum.usd
    let ethValue = bg(ethSum).times(ethPrice).toNumber();
    setAccountEthTokenValue(ethValue)
    setAccountDeriToken(deriSum)
  }, [BTokenContract, stakedTokenIdList, PairContract])

  const getApy = useCallback(async () => {
    if (totalYearDeri && accountDeriToken && accountEthTokenValue) {
      let deriPath = `${process.env.REACT_APP_INFO_HTTP_URL}/stats_for_token`
      let resp = await fetch(deriPath);
      let res = await resp.json();
      let deriPrice = res.data.price;
      let deriValue = bg(accountDeriToken).times(deriPrice).toNumber();
      let sumValue = deriValue + accountEthTokenValue
      let totalYearDeriValue = bg(totalYearDeri).times(deriPrice).toNumber();
      let apy = (totalYearDeriValue / sumValue) * 100
      setApy(apy)
    }
  }, [totalYearDeri, accountDeriToken, accountEthTokenValue])

  const getTimeRewardInfo = useCallback(async () => {
    let rewards = bTimeRewardInfo.map(async (item) => {
      let aTime = aTimeRewardInfo.find(id => id.id === item.id)
      let secondsPerLiquidityInsideX128_a = aTime.secondsPerLiquidityInsideX128
      let reward = await PoolContract.getRewardInBetween(item.id, item.timestamp, secondsPerLiquidityInsideX128_a, item.secondsPerLiquidityInsideX128)
      if (reward) {
        let obj = {
          reward: BigNumber.from(reward._hex).toString() / 10 ** 18,
          id: item.id,
          timestamp: item.timestamp - aTime.timestamp
        }
        return obj
      }
    })
    rewards = await Promise.all(rewards)
    if (rewards[0].timestamp !== 0) {
      const totalScore = rewards.reduce((accumulator, currentValue) => {
        return accumulator + currentValue.reward;
      }, 0);
      const oneYear = 1000 * 60 * 60 * 24 * 365;
      const valuePerYear = (totalScore / (rewards[0].timestamp * 1000)) * oneYear;
      setTotalYearDeri(valuePerYear)
    }
  }, [aTimeRewardInfo, bTimeRewardInfo, PoolContract])
  const afterTransaction = (activeIndex) => {
    getPendingReward()
    getunStakedTokenIdList()
    getStakedTokenIdList()
    getLiquidity()
    setActiveIndex(activeIndex)
  }
  const tabSet = useMemo(() => ({
    [lang['add']]: {
      content: <Operate type='add' getLang={getLang} afterTransaction={afterTransaction} wallet={wallet} alert={alert} address={address} lang={lang} NFTContract={NFTContract} PoolContract={PoolContract} tokenIdList={unStakedTokenIdList} />,
      style: { background: "linear-gradient(200.74deg, rgba(97, 122, 255, 0.1) -4.65%, rgba(223, 229, 255, 0) 50.94%, rgba(85, 91, 237, 0.05) 105.4%)" },
      borderColor: "#203B60",
      icon: 'add-icon',
      iconWidth: '25',
      iconHeight: '25',
      inicon: 'inactive-add',
      direction: "tab-left"
    },
    [lang['remove']]: {
      content: <Operate type='remove' getLang={getLang} afterTransaction={afterTransaction} wallet={wallet} alert={alert} address={address} lang={lang} PoolContract={PoolContract} NFTContract={NFTContract} tokenIdList={stakedTokenIdList} />,
      style: { background: "linear-gradient(200.74deg, rgba(97, 122, 255, 0.1) -4.65%, rgba(223, 229, 255, 0) 50.94%, rgba(85, 91, 237, 0.05) 105.4%)" },
      borderColor: "#203B60",
      icon: 'remove-icon',
      iconWidth: '25',
      iconHeight: '25',
      iconMarginRight: '20',
      inicon: 'inactive-remove',
      direction: "tab-right",
    },
  }), [lang, wallet, getLang, afterTransaction, unStakedTokenIdList, alert, stakedTokenIdList, NFTContract, PoolContract, address])

  const load = async () => {
    await poolManager.loadDoubleMingingPool();
    let pool = poolManager.lpPools
    pool = pool.filter(p => p.address === address)
    pool = pool[0]
    let symbols = pool.symbols.map(item => {
      let arr = [];
      arr.push(item.symbol)
      return arr
    })
    symbols = symbols.join(',')
    setPoolConfig(pool)
    setBTokenList(pool.bTokens)
  }
  const claimDeri = async () => {
    if (+poolDeri === 0) {
      alert.error(lang['no-deri-to-claim-yet']);
      return;
    }
    setClaimPending(true)
    const title = {
      text: "Claim Deri",
      icon: "DERI",
    }
    const content = {
      success: lang['claim-deri-reward'],
      error: lang['transaction-failed'],
    }
    try {
      let params = [address]
      let gasLimit = await getGasLimit(PoolContract, "claim", params)
      let res = await PoolContract.claim({ gasLimit: gasLimit });
      console.log("claim", res)
      if (res && res.hash) {
        Emitter.emit(EVENT_TRANS_BEGIN, {
          title: title, content: content, hash: res.hash
        })
      } else {
        getPendingReward()
        setClaimPending(false)
        return false
      }
      const receipt = await res.wait()
      console.log("receipt", receipt)
      if (receipt) {
        console.log("receipt", receipt)
        if (receipt && receipt.transactionHash) {
          const hash = receipt.transactionHash
          if (receipt.status) {
            Emitter.emit(EVENT_TRANS_END, { title: title, content: content, context: { success: true, hash: hash, error: "error" } })
            if (res.success) {
              setPoolDeri(0)
            }
          } else {
            Emitter.emit(EVENT_TRANS_END, { title: title, content: content, context: { success: false.success, hash: hash, error: "error" } })
          }
        }
      }
    } catch (error) {
      console.error(error)
    }
    getPendingReward()
    setClaimPending(false)
  }

  const getunStakedTokenIdList = useCallback(async () => {
    if (wallet.detail.account) {
      let NFTNumber = await NFTContract.balanceOf(wallet.detail.account)
      NFTNumber = BigNumber.from(NFTNumber._hex).toString()
      let tokenId = []
      for (let index = 0; index < NFTNumber; index++) {
        let id = await NFTContract.tokenOfOwnerByIndex(wallet.detail.account, index)
        id = BigNumber.from(id._hex).toString()
        tokenId.push(id)
      }
      let deriTokenId = [];
      for (let index = 0; index < tokenId.length; index++) {
        let id = await PoolContract.getPositionInfo(tokenId[index])
        if (id[0].toLocaleLowerCase() === PairAddress.toLocaleLowerCase()) {
          deriTokenId.push(tokenId[index])
        }
      }
      setUnStakedTokenIdList(deriTokenId)
    }
  }, [NFTContract, wallet.detail.account, PoolContract])

  const getStakedTokenIdList = useCallback(async () => {
    if (wallet.detail.account) {
      let res = await PoolContract.getUserTokenIds(wallet.detail.account)
      res = res.map(item => {
        return BigNumber.from(item._hex).toString()
      })
      console.log("getStakedTokenIdList", res)
      setStakedTokenIdList(res)
    }
  }, [PoolContract, wallet.detail.account])

  const getLiquidity = useCallback(async () => {
    let DeriNumber = await DeriforPairContract.balanceOf(PairAddress)
    let EthNumber = await EthforPairContract.balanceOf(PairAddress)
    let deriPath = `${process.env.REACT_APP_INFO_HTTP_URL}/stats_for_token`
    let ethPath = "https://api.coingecko.com/api/v3/simple/price?ids=ethereum&vs_currencies=USD"
    let resp = await fetch(deriPath);
    let res = await resp.json();
    let eresp = await fetch(ethPath);
    let eres = await eresp.json();
    let deriPrice = res.data.price;
    let ethPrice = eres.ethereum.usd
    let deriAmount = BigNumber.from(DeriNumber._hex).toString() / (10 ** 18);
    let ethAmount = BigNumber.from(EthNumber._hex).toString() / (10 ** 18);
    let deriValue = bg(deriAmount).times(deriPrice).toNumber();
    let ethValue = bg(ethAmount).times(ethPrice).toNumber();
    let liquidity = deriValue + ethValue
    setLiquidity(liquidity)
  }, [DeriforPairContract, EthforPairContract])

  useEffect(() => {
    if (wallet.detail.chainId && wallet.detail.chainId !== chainId) {
      wallet.switchNetwork(config[chainId])
    }
    if (wallet.isConnected() && PoolContract) {
      let interval = window.setInterval(getPendingReward, 1000 * 10 * 3);
      getPendingReward()
      return () => clearInterval(interval);
    }
  }, [wallet.detail.chainId, wallet.detail.account, PoolContract])


  // useEffect(() => {
  //   if (stakedTokenIdList.length && PoolContract) {
  //     aTime()
  //   }
  // }, [stakedTokenIdList, PoolContract, wallet])

  // useEffect(() => {
  //   if (stakedTokenIdList.length && BTokenContract && PairContract) {
  //     getBTokenValue()
  //   }
  // }, [BTokenContract, stakedTokenIdList, PairContract, wallet])

  // useEffect(() => {
  //   if (totalYearDeri && accountDeriToken && accountEthTokenValue) {
  //     getApy()
  //   }
  // }, [totalYearDeri, accountDeriToken, accountEthTokenValue])

  // useEffect(() => {
  //   if (aTimeRewardInfo.length && PoolContract && bTimeRewardInfo.length) {
  //     getTimeRewardInfo()
  //   }
  // }, [aTimeRewardInfo, bTimeRewardInfo, PoolContract])

  useEffect(() => {
    load()
  }, [])

  useEffect(() => {
    if (!wallet.isConnected() && liquidity && !stakedTokenIdList) {
      setApy(12.43)
    }
  }, [liquidity, wallet && stakedTokenIdList])

  useEffect(() => {
    if (PoolContract && !tokenIdListRef.current && wallet.detail.account) {
      getStakedTokenIdList()
      tokenIdListRef.current = true
    }
  }, [PoolContract, wallet.detail.account])

  useEffect(() => {
    if (NFTContract && wallet.detail.account && PoolContract && !unTokenIdListRef.current) {
      getunStakedTokenIdList()
      tokenIdListRef.current = true
    }
  }, [NFTContract, wallet.detail.account, PoolContract])

  useEffect(() => {
    if (DeriforPairContract && EthforPairContract) {
      getLiquidity()
    }
  }, [DeriforPairContract, EthforPairContract])


  return (
    <div className='pool-detail'>
      <div className='back-pool'>
        <div className='icon-back' onClick={() => { window.history.back(-1) }}>
          <img alt='' src={backIcon} />
          <span>{lang['back']}</span>
        </div>
      </div>
      <div className='pool-deatail-info'>
        <div className='pool-detail-basetoken-info'>
          <div className='pool-info-text'>
            <div className='pool-network-type'>
              <div className='network-logo'>
                <img alt='' src={ARBITRUM} />
              </div>
              <div className='pool-type-network-text'>
                <div className='pool-type-text'>
                  {lang[poolConfig.type]} {lang[poolConfig.zone]}
                </div>
                <div className='pool-network-text'>
                  {networkText}
                </div>
              </div>
            </div>
            <div className='pool-des'>
              Stake Uniswap <a rel='norelerror' target='_blank' href="https://app.uniswap.org/#/add/ETH/0x21E60EE73F17AC0A411ae5D690f908c3ED66Fe12/3000" >DERI-ETH </a> LP NFTs to earn $DERI rewards.
              <br></br>
              <a rel='norelerror' target='_blank' href='https://docs.deri.io/library/faqs/mining-faq#is-dex-liquidity-mining-on-sushiswap-sushiswap-onsen-and-pancakeswap-deri-risk-free'><span className='learn-more'> {lang['learn-more']}</span></a>
            </div>
          </div>
          <div className='select-basetoken-info'>
            <div className='select-coin-title-search'>
              <div className='select-coin-title'> {lang['select-the-coin']}</div>
              <div className='search-box'>
                <Search list={bTokenList} searchName='bTokenSymbol' onChange={() => { }} />
              </div>
            </div>
            <div className='coin-search'>
              <div className='slide-icon left'>
                <Icon token='arrow-left' width='24' height='24' />
              </div>
              <div className="coin-list">
                {bTokenList.length ? <>
                  <div className='coin-box checked-base'>
                    <div className='base-apy-info'>
                      <div className='lp-btoken-logo'>
                        <div className={`btoken-logo ${bTokenList[0].bTokenSymbol}`}></div>
                        <span className='conect-logo'>+</span>
                        <div className={`btoken-logo ${bTokenList[1].bTokenSymbol}`}></div>
                      </div>
                      <div className='apy-base-staked'>
                        <div className='base-name'>{nowToken}</div>
                        <div className='base-apy'>
                          {lang['apy']} &nbsp; <DeriNumberFormat value={apy} suffix='%' decimalScale={2} />
                        </div>
                      </div>
                    </div>
                  </div>
                </> : null}
              </div>
              <div className='slide-icon right'>
                <Icon token='arrow-right' width='24' height='24' />
              </div>
            </div>
          </div>
        </div>
        <div className='pool-detail-add-remove-info'>
          <div className='mining-deri-liquidity'>
            <div className='btoken-liquidity'>
              {bTokenList.length && <>
                <div className='lp-btoken-logo'>
                  <div className={`btoken-logo ${bTokenList[0].bTokenSymbol}`}></div>
                  <span className='conect-logo'>+</span>
                  <div className={`btoken-logo ${bTokenList[1].bTokenSymbol}`}></div>
                </div>
                <div className='manage-stake'>
                  <div className='manage'>
                    {lang['manage']} {nowToken} {lang['liquidity']}
                  </div>
                </div>
              </>}
            </div>
            <div className='deri-current-epoch'>
              <div className={'mining-pool-claim'}>
                <div className='mining-claim-info'>
                  <span> {lang['you-have']}</span>
                  <span className={`${+poolDeri === 0 ? 'no-deri' : ""} mining-pool-num`} onClick={claimDeri}>
                    {claimPending && <Spinner />}
                    <DeriNumberFormat firstTime={false} value={poolDeri} decimalScale={2} />
                    <img src={deri} alt="deri" />
                  </span>
                  <span>{lang['claim-in-pool']}</span>
                </div>
              </div>
            </div>
          </div>
          <div className='mining-add-remove-liquidity'>
            <div className='pool-info'>
              <div className='pool-info-title'>
                <div className='pool-info-title-text'>
                  {lang['pool-info']}
                </div>
                <div className='down-up'>
                  {isShowInfo === 'show' && <span onClick={() => setIsShowInfo('hide')}>
                    <img className='up-icon' alt='' src={downIcon} />
                  </span>}
                  {isShowInfo === 'hide' && <span onClick={() => setIsShowInfo('show')}>
                    <img alt='' src={downIcon} />
                  </span>}
                </div>
              </div>
              <div className={`'pool-info-text' ${isShowInfo}`}>
                <div className='pool-info-text-one'>
                  <div className='info-box'>
                    <div className='info-box-title'>{lang['apy']}</div>
                    <div className='info-box-num'>
                      <DeriNumberFormat value={apy} suffix='%' decimalScale={2} />
                    </div>
                  </div>
                  <div className='info-box'>
                    <div className='info-box-title'>{lang['pool-liquidity']}</div>
                    <div className='info-box-num'>
                      $ <DeriNumberFormat value={liquidity} displayType='text' decimalScale={0} thousandSeparator={true} />
                    </div>
                  </div>
                  <div className='info-box'>
                    <div className='info-box-title'>{lang['staked-balance']}</div>
                    <div className='info-box-num'>
                      {stakedTokenIdList.length}{" "}
                      <a href='https://arbiscan.io/address/0xC36442b4a4522E871399CD717aBDD847Ab11FE88'>DERI/ETH LP NFTs</a>
                    </div>
                  </div>
                </div>
                <div className='pool-info-text-two'>
                  <div className='info-box'>
                    <div className='info-box-title'>{lang['contract-address']}</div>
                    <div className='info-box-num'>
                      <a target='_blank' rel='noreferrer' href={`${chainConfig[chainId] && chainConfig[chainId]['viewUrl']}/address/${address}`}>
                        {formatAddress(address)}
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className='add-remove-liquidity'>
              <Tab tabSet={tabSet} iconMarginRight='20' width={379} size={18} inactiveBgColor='#0c192d' icon='' borderBottom={true} divider={true} activeIndex={activeIndex} />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
const Operate = ({ wallet, type, lang, getLang, afterTransaction, tokenIdList, alert, NFTContract, PoolContract, address }) => {
  const [disabled, setDisabled] = useState(false)
  const [isApproveList, setIsApproveList] = useState([])
  const [isApproved, setIsApproved] = useState(false)
  const [approveBtnText, setApproveBtnText] = useState(lang["approve"])
  const NFTcontractCall = useContractCall(NFTContract);
  // const PoolcontractCall = useContractCall(PoolContract);
  const colors = ['#824A4A', '#69985E', '#93A1B8', '#377DFF', '#59AE99'];
  const isApprove = useCallback(async () => {
    let list = []
    for (let index = 0; index < tokenIdList.length; index++) {
      let approve = await NFTContract.getApproved(tokenIdList[index])
      console.log("getApproved", approve)
      let obj = {}
      if (approve && approve.toLocaleLowerCase() === address.toLocaleLowerCase()) {
        obj = {
          id: tokenIdList[index],
          isApproved: true
        }
      } else {
        obj = {
          id: tokenIdList[index],
          isApproved: false
        }
      }
      list.push(obj)
    }
    console.log(list)
    setIsApproveList(list)
  }, [tokenIdList, NFTContract])

  const approve = useCallback(async (id) => {
    const transactionTitle = {
      text: "Approve",
      icon: "DERI"
    }
    const transactionContent = {
      success: `Approve ${id}`,
      error: "Transaction failed",
    }
    id = +id
    try {
      let params = [address, id]
      let gasLimit = await getGasLimit(NFTContract, "approve", params)
      console.log(gasLimit)
      let res = await NFTcontractCall("approve", params, { gasLimit: gasLimit });
      // let transactionResponse = await NFTContract.approve(address, id)
      console.log("approve", res)
      if (res && res.hash) {
        Emitter.emit(EVENT_TRANS_BEGIN, {
          title: transactionTitle, content: transactionContent, hash: res.hash
        })
      } else {
        return false
      }
      const receipt = await res.wait()
      console.log("receipt", receipt)
      if (receipt) {
        console.log("receipt", receipt)
        if (receipt && receipt.transactionHash) {
          const hash = receipt.transactionHash
          if (receipt.status) {
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: true, hash: hash, error: "error" } })
            return true
          } else {
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: false.success, hash: hash, error: "error" } })
            return false
          }

        }
      }
    } catch (error) {
      console.error(error)
      return false
    }
  }, [NFTContract])

  const clickApprove = useCallback(async () => {
    let id = isApproveList.find(item => !item.isApproved)
    if (id) {
      let approved = await approve(id.id)
      isApprove()
    }
  }, [isApproveList])

  const addLiquidity = useCallback(async () => {
    const title = {
      text: lang["add-liquidity-state"],
      icon: "DERI"
    }
    const connect = {
      success: `${getLang('add-baseToken-liquidity', { baseToken: "UNISWAP-LP" })}`,
      error: lang['transaction-failed'],
    }
    try {
      let gasLimit = await getGasLimit(PoolContract, "deposit", [])
      let res = await PoolContract["deposit()"]({ gasLimit: gasLimit });
      console.log("deposit", res)
      if (res && res.hash) {
        Emitter.emit(EVENT_TRANS_BEGIN, {
          title: title, content: connect, hash: res.hash
        })
      } else {
        return false
      }
      const receipt = await res.wait()
      console.log("receipt", receipt)
      if (receipt) {
        console.log("receipt", receipt)
        if (receipt && receipt.transactionHash) {
          const hash = receipt.transactionHash
          let index = type === "add" ? 0 : 1
          afterTransaction && afterTransaction(index)
          if (receipt.status) {
            Emitter.emit(EVENT_TRANS_END, { title: title, content: connect, context: { success: true, hash: hash, error: "error" } })
            return true
          } else {
            Emitter.emit(EVENT_TRANS_END, { title: title, content: connect, context: { success: false.success, hash: hash, error: "error" } })
            return false
          }

        }
      }
    } catch (error) {
      console.error(error)
      return false
    }

  }, [PoolContract, type])
  const removeLiquidity = useCallback(async () => {
    const title = {
      text: lang["remove-liquidity-state"],
      icon: "DERI"
    }
    const connect = {
      success: `${getLang('remove-baseToken-liquidity', { baseToken: "UNISWAP-LP" })}`,
      error: lang['transaction-failed'],
    }
    try {
      let gasLimit = await getGasLimit(PoolContract, "withdraw", [])
      let res = await PoolContract["withdraw()"]({ gasLimit: gasLimit });
      console.log("withdraw", res)
      if (res && res.hash) {
        Emitter.emit(EVENT_TRANS_BEGIN, {
          title: title, content: connect, hash: res.hash
        })
      } else {
        return false
      }
      const receipt = await res.wait()
      console.log("receipt", receipt)
      if (receipt) {
        console.log("receipt", receipt)
        let index = type === "add" ? 0 : 1
        afterTransaction && afterTransaction(index)
        if (receipt && receipt.transactionHash) {
          const hash = receipt.transactionHash
          if (receipt.status) {
            Emitter.emit(EVENT_TRANS_END, { title: title, content: connect, context: { success: true, hash: hash, error: "error" } })
            return true
          } else {
            Emitter.emit(EVENT_TRANS_END, { title: title, content: connect, context: { success: false.success, hash: hash, error: "error" } })
            return false
          }
        }
      }
    } catch (error) {
      console.error(error)
      return false
    }

  }, [PoolContract, type])

  useEffect(() => {
    if (isApproveList.length) {
      let id = isApproveList.find(item => !item.isApproved)
      if (id) {
        let text = `${lang["approve"]} ${id.id}`
        setApproveBtnText(text)
      } else {
        setIsApproved(true)
      }
    }
  }, [isApproveList])

  useEffect(() => {
    if (NFTContract && tokenIdList && tokenIdList.length && type === 'add') {
      isApprove()
    }
  }, [tokenIdList, NFTContract, type])

  useEffect(() => {
    setDisabled(!tokenIdList.length)
    setIsApproved(!tokenIdList.length)
    if (type === "remove") {
      setIsApproved(true)
    }
  }, [tokenIdList, type])
  return (
    <div className='operate-liquidity-lp operate-liquidity'>
      {
        (tokenIdList && tokenIdList.length) ?
          <div className='add-remove-nft-box'>
            <div className='unstaked-nft'>
              Your {type === "add" ? "unstaked" : "staked"} DERI/ETH LP NFTs
            </div>
            <div className='tokenIdList'>
              {tokenIdList && tokenIdList.map((item, index) => {
                return (
                  <div className='tokenId-box' key="index">
                    <div className='color-box' style={{ backgroundColor: colors[index] }}></div>
                    <div className='tokenId-text'>{item}</div>
                  </div>
                )
              })}
            </div>
          </div>
          :
          <div className='no-nft-box'>
            <img src={noNFT} alt="" />
            <div>
              {type === "add" ? "it seems you did not have DERI-ETH LP NFT in your wallet" : "it seems you did not stake any  DERI-ETH LP NFT"}
            </div>
          </div>
      }

      <div className='add-remove-btn'>
        {!isApproved ? <Button width="331" fontSize='18' disabled={disabled} label={approveBtnText} type={PRIMARY} onClick={clickApprove} /> : <Button width="331" fontSize='18' disabled={disabled} label={type === "add" ? lang["add-liquidity"] : lang["remove-liquidity"]} type={PRIMARY} onClick={type === "add" ? addLiquidity : removeLiquidity} />}
      </div>
    </div >
  )
}
export default inject('wallet', 'poolManager')(observer(LpDetail))